import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import Router from 'next/router';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';
import Error from './ErrorMessage';

const CREATE_PRODUCT_MUTATION = gql`
  mutation CREATE_PRODUCT_MUTATION(
    $title: String!
    $description: String!
    $price: Int!
    $originalPrice: Int!
    $discount: Int
    $image: String
    $largeImage: String
  ) {
    createProduct(
      title: $title
      description: $description
      price: $price
      originalPrice: $originalPrice
      discount: $discount
      image: $image
      largeImage: $largeImage
    ) {
      id
    }
  }
`;

class CreateProduct extends Component {
    state = {
        title: 'Rolex',
        description: 'A brand new rolex wrist watch',
        image: '',
        largeImage: '',
        price: 500,
        originalPrice: 1000,
        discount: 0.5,
    };

    handleChange = e => {
        const { name, type, value } = e.target;
        const val = type === 'number' ? parseFloat(value) : value;
        this.setState({ [name]: val });
      };

    uploadFile = async e => {
        const files = e.target.files;
        const data = new FormData();
        data.append('file', files[0]);
        data.append('upload_preset', 'price-deal');

        const res = await fetch('https://api.cloudinary.com/v1_1/inventive-labs/image/upload', {
            method: 'POST',
            body: data,
        });
        const file = await res.json();
        console.log(file);
        this.setState({
            image: file.secure_url,
            largeImage: file.eager[0].secure_url,
        });
    };
    
    render() {
        return (
            <Mutation mutation={CREATE_PRODUCT_MUTATION} variables={this.state}>
                {(createProduct, { loading, error }) => (
                <Form
                    data-test="form"
                    onSubmit={async e => {
                    // Stop the form from submitting
                        e.preventDefault();
                        // call the mutation
                        const res = await createProduct();
                        // change them to the single Product page
                        console.log(res);
                        Router.push({
                            pathname: '/product',
                            query: { id: res.data.createProduct.id },
                        });
                    }}
                >
                    <Error error={error} />
                    <fieldset disabled={loading} aria-busy={loading}>
                        <label htmlFor="file">
                            Image
                            <input
                            type="file"
                            id="file"
                            name="file"
                            placeholder="Upload an image"
                            required
                            onChange={this.uploadFile}
                            />
                            {this.state.image && (
                            <img width="200" src={this.state.image} alt="Upload Preview" />
                            )}
                        </label>
                        <label htmlFor="title">
                            Title
                            <input
                            type="text"
                            id="title"
                            name="title"
                            placeholder="Title"
                            required
                            value={this.state.title}
                            onChange={this.handleChange}
                            />
                        </label>
            
                        <label htmlFor="description">
                            Description
                            <textarea
                            id="description"
                            name="description"
                            placeholder="Enter A Description"
                            required
                            value={this.state.description}
                            onChange={this.handleChange}
                            />
                        </label>
                        
                        <label htmlFor="originalprice">
                            Original Price
                            <input
                            type="number"
                            id="originalPrice"
                            name="originalPrice"
                            placeholder="originalPrice"
                            required
                            value={this.state.originalPrice}
                            onChange={this.handleChange}
                            />
                        </label>

                        <label htmlFor="discount">
                            Discount
                            <input
                            type="number"
                            id="discount"
                            name="discount"
                            placeholder="discount"
                            required
                            value={this.state.discount}
                            onChange={this.handleChange}
                            />
                        </label>

                        <label htmlFor="price">
                            Price
                            <input
                            type="number"
                            id="price"
                            name="price"
                            placeholder="Price"
                            required
                            value={this.state.price}
                            onChange={this.handleChange}
                            />
                        </label>

                        
                        <button type="submit">Submit</button>
                    </fieldset>
                </Form>
                )}
            </Mutation>
        );
    }
}
export default CreateProduct;
export { CREATE_PRODUCT_MUTATION };